@import "~react-image-gallery/styles/css/image-gallery.css";
@import "assets/scss/style-rtl.scss";
@import "assets/scss/override-style.scss";

/* cairo-regular - latin_arabic */
@font-face {
  font-family: 'Cairo';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Cairo/cairo-v20-latin_arabic-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('./assets/fonts/Cairo/cairo-v20-latin_arabic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('./assets/fonts/Cairo/cairo-v20-latin_arabic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('./assets/fonts/Cairo/cairo-v20-latin_arabic-regular.woff') format('woff'), /* Modern Browsers */
       url('./assets/fonts/Cairo/cairo-v20-latin_arabic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('./assets/fonts/Cairo/cairo-v20-latin_arabic-regular.svg#Cairo') format('svg'); /* Legacy iOS */
}
 
*{
  font-family: 'FFShamel';
}

div{
  white-space: normal !important
}

div[id^=column]{
  text-align: center;
}


.node-container {
  display: flex;
  align-items: center; /* محاذاة العناصر عمودياً في المنتصف */
  gap: 8px; /* مسافة بين العناصر */
}

.node-actions {
  display: flex;
  gap: 8px; /* مسافة بين الأيقونات */
  margin-left: auto; /* دفع الأيقونات إلى أقصى اليمين */
}

ul {
  list-style-type: none; /* إزالة النقاط من القائمة */
  padding-left: 20px; /* مسافة بادئة للعقد الفرعية */
}

li {
  margin: 4px 0; /* مسافة بين العناصر */
}
.node-bullet {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: "primary"; /* لون الخلفية */
  color: white; /* لون النص */
  border-radius: 50%; /* جعلها دائرية */
  text-align: center;
  line-height: 20px; /* محاذاة النص عمودياً */
  font-size: 14px;
  font-weight: bold;
}