.main-menu {
  display: flex;
  height: 100vh;
  background: #f8f9fa;

  .menu-left {
    width: 80px; 
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;

    .navigation {
      list-style: none;
      padding: 0;
      margin: 0;
      width: 100%;

      .menu-item {
        width: 100%;
        color: #fff;
        text-align: center;
        padding: 15px 0;
        cursor: pointer;
        transition: background 0.3s;

        &:hover,
        &.active {
          background: #FFF;
        }

        span {
          display: block;
          font-size: 12px;
          margin-top: 5px;
        }
      }
    }
  }

  .menu-right {
    width: 200px;
    height: 100vh;
    background: #f8f9fa;
    padding: 10px;
    /* box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); */
    display: flex;
    flex-direction: column;

    .submenu-scroll {
      max-height: calc(100vh - 20px);
      overflow-y: auto;
    }

    .submenu-item {
      display: block;
      padding: 10px;
      color: #212529;
      text-decoration: none;
      transition: background 0.3s;

      &:hover {
        background: #e9ecef;
        border-radius: 5px;
      }
    }
  }
}
